<template>
	<div class="home">
		<div class="nav">
			<a v-for="(i,$index) in data" :key="$index" :href="'#'+i.title" style="display: block;">{{i.title}}</a>
		</div>
		<div class="main" style="height: 1500px;">
			<div v-for="(i,$index) in data" :key="$index">
				<div style="color: #000; text-decoration: none;display: block;line-height: 30px;background: #CCCCCC;" :id="i.title">{{i.title}}</div>
				<div v-for="(w,$index) in i.arr" :key="$index" style="padding: 10px; background: #42B983;margin-bottom: 10px; overflow: hidden;">
					<!-- <router-link :to="'/info/'+ i.title +'/'+ $index" style="display:block;text-decoration:none;"> -->
						<div style="width: 80px; height: 80px; overflow: hidden; float: left; margin-right: 10px;">
							<img :src="w.url" alt="">
						</div>
						<div style="font-size: 14px;margin-bottom: 20px;">{{w.title}}</div>
						<p style="font-size: 14px;">单价:{{w.jg}}元</p>
						<sumshopper :value="w.count" @input="addItem(w,$event)"></sumshopper>
					<!-- </router-link> -->
				</div>
			</div>
		</div>
		<div class="foot" style="width: 100%; background: orange; height: 80px; position: fixed;bottom: 0;">
			<div style="float: left;padding-left: 20px; line-height: 80px; color: #FFFFFF;">总价：{{sumItem}}元</div>
			<router-link to="/about" style="display: block;width: 100px; height: 80px;float: right;">
				<div style="width: 100px; height: 80px;line-height: 80px;color: #FFFFFF;text-align: center;background: orangered;">去结算</div>
			</router-link>
		</div>
	</div>
</template>

<script>
	import sumshopper from '../components/computed.vue'
	import {
		mapGetters
	} from 'vuex'
	export default {
		components: {
			sumshopper
		},
		data() {
			return {
				data: [
		{
			"title": "水饺",
			"arr": [{
				"url": "https://i02piccdn.sogoucdn.com/ab27463db7adadd9",
				"title": "猪肉白菜水饺",
				"ice": "月售18份  好评率100%",
				"jg": 16,
				"count":0
			}, {
				"url": "https://i04piccdn.sogoucdn.com/9a8a859cbf7fd144",
				"title": "鱿鱼三鲜水饺",
				"ice": "月售18份  好评率100%",
				"jg": 20,
				"count":0
			}, {
				"url": "http://img5.imgtn.bdimg.com/it/u=147737653,2351889088&fm=26&gp=0.jpg",
				"title": "虾仁三鲜水饺",
				"ice": "月售19份  好评率100%",
				"jg": 25,
				"count":0
			}, {
				"url": "https://i02piccdn.sogoucdn.com/a4e37302a9b6143d",
				"title": "南瓜皮菌菇三鲜猪肉水饺",
				"ice": "月售19份  好评率100%",
				"jg": 24,
				"js": "香菇、猪肉、鸡蛋、木耳、饺子皮",
				"count":0
			}]
		},
		{
			"title": "流食",
			"arr": [{
					"url": "https://i01piccdn.sogoucdn.com/a298b57577b27941",
					"title": "鱿鱼三鲜水饺",
					"ice": "月售18份  好评率100%",
					"jg": 20,
					"count":0
				},
				{
					"url": "https://i04piccdn.sogoucdn.com/4528cf890080e280",
					"title": "南瓜皮菌菇三鲜猪肉水饺",
					"ice": "月售19份  好评率100%",
					"jg": 24,
					"js": "香菇、猪肉、鸡蛋、木耳、饺子皮",
					"count":0
				}
			]
		},
		{
			"title": "凉菜",
			"arr": [{
					"url": "http://img0.imgtn.bdimg.com/it/u=1126766578,3546096906&fm=26&gp=0.jpg",
					"title": "虾仁三鲜水饺",
					"ice": "月售19份  好评率100%",
					"jg": 25,
					"count":0
				},
				{
					"url": "https://i03piccdn.sogoucdn.com/f3930ba430828bd1",
					"title": "猪肉白菜水饺",
					"ice": "月售18份  好评率100%",
					"jg": 16,
					"count":0
				}
			]
		}
	] ,
				arr: []
			}
		},
		watch: {
			'data': {
				handler(newval) {
					this.arr = []
					newval.forEach((item) => {
						item.arr.forEach((every) => {
							if (every.count != 0) {
								this.arr.push(every)
							}
						})
					})
					this.$store.commit('addEvery', this.arr)
				},
				deep: true
			}
		},
		methods: {
			addItem(item, ev) {
				item.count = ev
			}
		},
		computed: {
			...mapGetters(['sumItem'])
		},
		created() {
			this.$http.get('index.json').then((res) => {
				this.data = res.data.arr
			})
		}
	}
</script>
<style>
	.home {
		overflow: hidden;
	}

	.home .nav {
		padding: 30px 0;
		width: 20%;
		position: fixed;
		top: 10px;
		left: 0;
	}

	.home .nav>a {
		padding: 6px 10px;
		border-bottom: 1px #ccc solid;
		margin-bottom: 20px;
		text-align: center;
		background: #42B983;
		text-decoration: none;
	}

	.home .main {
		float: right;
		width: 80%;
		box-sizing: border-box;
		padding-left: 30px;
		padding-top: 30px;
	}

	.home .main>div img {
		width: 80px;
		height: 80px;
	}
</style>
