<template>
	<div id="computed">
		<div class="btn" @click.stop="godown">-</div>
		<input type="text" style="width: 20px;height: 20px; text-align: center;float: left;" v-model="val">
		<div class="btn" @click.stop="goup">+</div>
	</div>
</template>

<script>
	export default {
		props:['value'],
		computed:{
			val:{
				get(){
					return this.value
				},
				set(v){
					this.$emit('input',v)
				}
			}
		},
		methods:{
			goup(){
				this.val++
			},
			godown(){
				this.val = this.val <= 0 ? 0 : this.val  - 1
			}
		}
	}
</script>

<style>
	#computed {
		overflow: hidden;
	}

	#computed>div {
		float: left;
		text-align: center;
		color: #FFFFFF;
		font-weight: bold;
	}

	#computed .btn {
		width: 20px;
		height: 20px;
		border-radius: 50%;
		color: #fff;
		background: #2C3E50;
		line-height: 20px;
		text-align: center;
		cursor: pointer;
	}
</style>
