export default new Vuex.Store({
	state: {
		shoplist:[],
		arr:list,
		shop_info:''
	},
	mutations: {
		addEvery(state,item){
			state.shoplist = item
		},
		shopInfo(stat, json){
			// console.log(eval(stat.arr.arr))
			stat.shop_info = eval(stat.arr.arr).filter( (item) => 
				item.title == json.title
			)[0].arr.filter( (i, key) => 
				key == json.key
			)
		}
	},
	actions: {
		shopInfo({commit}, json){
			commit('shopInfo', json)
		}
	},
	getters:{
		sumItem(state){
			var n = 0
			state.shoplist.forEach((item)=>{
				n += parseInt(item.jg) * item.count
			})
			return n
		},
	}
})
